import { generateRemoteUrl, generateUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import { joinPaths, encodePath } from '@nextcloud/paths';
import moment from '@nextcloud/moment';
import axios from '@nextcloud/axios';
import client from "../utils/davClient.js";
import davRequest from "../utils/davRequest.js";
import logger from "../utils/logger.js";
export async function fetchVersions(fileInfo) {
  const path = `/versions/${getCurrentUser()?.uid}/versions/${fileInfo.id}`;
  try {
    const response = await client.getDirectoryContents(path, {
      data: davRequest,
      details: true
    });
    const versions = response.data
    // Filter out root
    .filter(_ref => {
      let {
        mime
      } = _ref;
      return mime !== '';
    }).map(version => formatVersion(version, fileInfo));
    const authorIds = new Set(versions.map(version => String(version.author)));
    const authors = await axios.post(generateUrl('/displaynames'), {
      users: [...authorIds]
    });
    for (const version of versions) {
      const author = authors.data.users[version.author ?? ''];
      if (author) {
        version.authorName = author;
      }
    }
    return versions;
  } catch (exception) {
    logger.error('Could not fetch version', {
      exception
    });
    throw exception;
  }
}
/**
 * Restore the given version
 */
export async function restoreVersion(version) {
  try {
    logger.debug('Restoring version', {
      url: version.url
    });
    await client.moveFile(`/versions/${getCurrentUser()?.uid}/versions/${version.fileId}/${version.fileVersion}`, `/versions/${getCurrentUser()?.uid}/restore/target`);
  } catch (exception) {
    logger.error('Could not restore version', {
      exception
    });
    throw exception;
  }
}
/**
 * Format version
 */
function formatVersion(version, fileInfo) {
  const mtime = moment(version.lastmod).unix() * 1000;
  let previewUrl = '';
  if (mtime === fileInfo.mtime) {
    // Version is the current one
    previewUrl = generateUrl('/core/preview?fileId={fileId}&c={fileEtag}&x=250&y=250&forceIcon=0&a=0&forceIcon=1&mimeFallback=1', {
      fileId: fileInfo.id,
      fileEtag: fileInfo.etag
    });
  } else {
    previewUrl = generateUrl('/apps/files_versions/preview?file={file}&version={fileVersion}&mimeFallback=1', {
      file: joinPaths(fileInfo.path, fileInfo.name),
      fileVersion: version.basename
    });
  }
  return {
    fileId: fileInfo.id,
    // If version-label is defined make sure it is a string (prevent issue if the label is a number an PHP returns a number then)
    label: version.props['version-label'] ? String(version.props['version-label']) : '',
    author: version.props['version-author'] ? String(version.props['version-author']) : null,
    authorName: null,
    filename: version.filename,
    basename: moment(mtime).format('LLL'),
    mime: version.mime,
    etag: `${version.props.getetag}`,
    size: version.size,
    type: version.type,
    mtime,
    permissions: 'R',
    previewUrl,
    url: joinPaths('/remote.php/dav', version.filename),
    source: generateRemoteUrl('dav') + encodePath(version.filename),
    fileVersion: version.basename
  };
}
export async function setVersionLabel(version, newLabel) {
  return await client.customRequest(version.filename, {
    method: 'PROPPATCH',
    data: `<?xml version="1.0"?>
					<d:propertyupdate xmlns:d="DAV:"
						xmlns:oc="http://owncloud.org/ns"
						xmlns:nc="http://nextcloud.org/ns"
						xmlns:ocs="http://open-collaboration-services.org/ns">
					<d:set>
						<d:prop>
							<nc:version-label>${newLabel}</nc:version-label>
						</d:prop>
					</d:set>
					</d:propertyupdate>`
  });
}
export async function deleteVersion(version) {
  await client.deleteFile(version.filename);
}